@import url('https://fonts.googleapis.com/css?family=Quicksand:400,500,700');

.set-screen {
    background: url("/img/register/bg.png");
    background-size: cover;
    background-repeat: no-repeat;
    padding: 7% 0 10% 0 ;
}

.profile-set
{
    padding-left: 30%; 
    padding-right: 30%;
}

.userpic 
{
    width: 120px;
}

.choice-group
{
    width:100%;
}

.choice
{
  background-color: #fff5ee;
  padding: 10% 0 10% 0;
}

.profile-box
{
    width: 100%;
    /* margin: 0 auto 0 auto;  */
    background-color: #f3ece7;
    border-radius: 12px;
    /* padding: 20px; */
    font-family: 'Quicksand', sans-serif; 
    min-width: 393px;
    padding: 20px 20px 50px 20px
}

a 
{
    color: #7b7b7b;
}

.menupro {
    width: 200px;
    border-radius: 8px;
    background-color: #f3ece7;
}

.menulink:hover {
    border-radius: 8px;
    background-color: #e4dac2;
}

.form
{
    font-size: 18px;
}

.form-left
{
    padding-left: 30px;
    padding-right: 30px;
}

.text-form-left
{
    width: 40%;
}

.form-right
{
    padding-left: 30px;
    padding-right: 30px;
}

.text-form-right
{
    text-align: left;
}

.payment
{
    width: 41%;
    text-align: left;
    padding-left: 15px;
}

.exp
{
    width: 20%;
    padding-left: 15px;
}

.button-confirm
{
    background-color: #e4dac2;
    color: #7b7b7b;
    border: solid 0px;
    width: 130px;
    font-weight: bold;
    /* margin-bottom: 20px; */
}

.addr-dropdown
{
    width: 60%;
    min-width: 100px;
}

input[type=text] {
    border-radius: 8px;
 }

input[type=password] {
    border-radius: 8px;
 }

input[type=email] {
    border-radius: 8px;
 }

input[type=textarea] {
    border-radius: 8px;
 }